@keyframes slideToRight {
  0% {
    opacity: 0;
    color: gray;
    transform: translateX(-5rem);
  }

  80% {
    transform: translateX(1.6rem);
  }

  100% {
    opacity: 1;
    color: #000;
    transform: translate(0);
  }
}

@keyframes slideToLeft {
  0% {
    opacity: 0;
    transform: translateX(100vw);
  }

  80% {
    transform: translateX(-20vw);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  50% {
    opacity: .6;
    transform: scaleY(1.3);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes dropAndBounce {
  0% {
    opacity: 0;
    transform: translateY(-3rem);
  }

  40% {
    transform: translate(0);
  }

  60% {
    transform: translateY(-1.5rem);
  }

  70% {
    transform: translate(0);
  }

  80% {
    transform: translateY(-.5rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes rainbowZoomIn {
  0% {
    color: gray;
    fill: gray;
    opacity: .17;
  }

  17% {
    color: #00f;
    fill: #00f;
    opacity: .34;
  }

  34% {
    color: green;
    fill: green;
    opacity: .51;
  }

  51% {
    color: #ff0;
    fill: #ff0;
    opacity: .68;
  }

  68% {
    color: #fff;
    fill: #fff;
    opacity: .85;
  }

  85% {
    color: red;
    fill: red;
    opacity: .95;
  }

  100% {
    color: #000;
    fill: #000;
    opacity: 1;
  }
}

@keyframes flip {
  0% {
    opacity: .1;
  }

  10% {
    opacity: .4;
    transform: skewY(180deg);
  }

  90% {
    opacity: .8;
  }

  100% {
    opacity: 1;
    transform: skew(0deg);
  }
}

@keyframes moveAndJump {
  20% {
    transform: translateX(10rem);
  }

  50% {
    transform: translate(0);
  }

  60% {
    transform: translateX(-10rem);
  }

  70% {
    transform: translate(0);
  }

  80% {
    transform: translateY(-2rem);
  }

  90% {
    transform: translate(0);
  }

  95% {
    transform: translateY(-.5rem);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes borderLtoR {
  from {
    border-bottom: 1px solid var(--color-white);
    border-bottom-width: 0;
    border-color: var(--color-secondary-200);
  }

  to {
    border-bottom: 1px solid var(--color-white);
    border-bottom-width: 2px;
    border-color: var(--color-secondary-400);
    box-shadow: 0 0 10px 5px #3498dbb3;
  }
}

@keyframes box-shadow-anim {
  0% {
    box-shadow: -1.5rem 1.5rem 2.8rem var(--color-secondary-950), 1.5rem -1.5rem 2.8rem var(--color-secondary-900);
  }

  20% {
    box-shadow: -1.5rem 1.5rem 2.6rem var(--color-secondary-900), 1.5rem -1.5rem 2.6rem var(--color-secondary-950);
  }

  40% {
    box-shadow: -1.5rem 1.5rem 2.8rem var(--color-secondary-800), 1.5rem -1.5rem 2.8rem var(--color-secondary-900);
  }

  60% {
    box-shadow: -1.5rem 1.5rem 3rem var(--color-secondary-900), 1.5rem -1.5rem 3rem var(--color-secondary-950);
  }

  80% {
    box-shadow: -1.5rem 1.5rem 2.8rem var(--color-secondary-950), 1.5rem -1.5rem 2.8rem var(--color-secondary-900);
  }

  100% {
    box-shadow: -1.5rem 1.5rem 3rem var(--color-secondary-900), 1.5rem -1.5rem 3rem var(--color-secondary-950);
  }
}

