.mainhome__main .navigation .navigation__list__item {
  border: 1px solid var(--color-black-replace);
  border-radius: 0%;
  transition: all .5s;
  position: relative;
}

.mainhome__main .navigation .navigation__list__item:before {
  content: "";
  background-color: var(--color-secondary-950);
  z-index: 1;
  width: 0%;
  height: 100%;
  transition: width .3s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.mainhome__main .navigation .navigation__list__item:hover:before {
  width: 50%;
}

.mainhome__main .navigation .navigation__list__item:after {
  content: "";
  background-color: var(--color-secondary-950);
  z-index: 1;
  width: 0%;
  height: 100%;
  transition: width .3s ease-out;
  position: absolute;
  top: 0;
  right: 0;
}

.mainhome__main .navigation .navigation__list__item:hover:after {
  width: 50%;
}

.mainhome__main .navigation .navigation__list__item:hover {
  background-color: var(--color-primary-300);
  color: var(--color-black);
  text-transform: uppercase;
}

