@use "../abstract/" as abs;

///////////
/// MAIN HOME PAGE
.mainhome {
	position: relative;

	&__header {
		background-color: var(--slate-color-800);

		&__title {
			height: 100vh;
			display: flex;
			justify-content: center;
			align-items: center;
			color: var(--nuetral-color-100);
			text-align: center;
			text-transform: uppercase;
			text-shadow: -3px 3px 3px var(--nuetral-color-400);
			font-size: 7.41rem;
			font-family: var(--font-primary);
			font-weight: 600;
			letter-spacing: -8;
			word-spacing: 1rem;
		}
	}

	&__main {
		.mainhome__section {
			&__header {
				width: 100%;
				margin-bottom: 5rem;
				color: var(--nuetral-color-300);
				text-shadow: 2px 2px 1px var(--nuetral-color-600);
				font-family: var(--font-primary);
				font-weight: 600;
				font-size: clamp(6rem, 10vw, 7rem);
				letter-spacing: -3;
				text-transform: uppercase;
				text-align: center;
				border-bottom: 5px double var(--gray-color-400);
			}
		}
	}
}

