	.nav--container {
		grid-template-rows: 10vh 6vh 75vh;
		display: grid;
		gap: 1rem;

		h1 {
			grid-column: 1 / -1;
			grid-row: 1 / 2;
			font-family: var(--font-primary);
			font-weight: 600;
			font-size: 5rem;
			text-transform: uppercase;
			letter-spacing: -2;
			word-spacing: 0.7rem;
			@extend %center-me-full;

			animation-name: rainbowZoomIn;
			animation-duration: 4s;
			animation-delay: 0.2s;
			animation-iteration-count: infinite;
			animation-direction: alternate-reverse;

			@include abs.mq(xxl) {
				font-size: 4.5rem;
			}
			@include abs.mq(xl) {
				font-size: 4rem;
			}
			@include abs.mq(lg) {
				font-size: 3.6rem;
			}
			@include abs.mq(md) {
				font-size: 3.2rem;
			}
			@include abs.mq(sm) {
				font-size: 2.8rem;
			}
			@include abs.mq(xs) {
				font-size: 2.4rem;
			}
			@include abs.mq(xxs) {
				font-size: 2rem;
			}
		}

		svg {
			grid-row: 2 / 3;
			justify-self: self-end;
			align-self: center;

			height: 4rem;
			width: 4rem;
			margin-right: 5rem;

			animation-name: rainbowZoomIn;
			animation-duration: 4s;
			animation-delay: 0.2s;
			animation-iteration-count: infinite;
			animation-direction: alternate-reverse;

