@use "extend" as ext;
@use "var";
@use "utility" as util;
@use "mixin";
@use "anim";
@use "bp";

////////////
/// content-page
.content__page--container {
	main section {
		font-size: 3rem;
		margin-bottom: 15rem;

		@include bp.mq(md) {
			margin-bottom: 10rem;
		}
		@include bp.mq(xs) {
			margin-bottom: 7rem;
		}

		article {
			margin-top: 1.5rem;
			margin-bottom: 15rem;
			background-color: var(--content-post-bg);
			// background-color: darken(var.$gray-color-200, 2%);

			@include bp.mq(md) {
				margin-bottom: 10rem;
			}
			@include bp.mq(sm) {
				margin-bottom: 7rem;
			}
		}

		.content__post {
			display: block;
			position: relative;
			padding: 5svh 15vw 0 7vw;
			line-height: 2;
			font-size: 2.7rem;
			font-family: var(--font-content);
			color: var(--stone-color-700);
			font-weight: 400;
			word-spacing: 0.5rem;
			max-width: 75ch;
			word-wrap: break-word;

			@include bp.mq(md) {
				padding: 0 0.8em;
				padding-left: 7vw;
			}
		}
		.post__code {
			font-family: var(--font-code);
			font-weight: 600;
			background-color: darken(var.$gray-color-300, 12%);
			@include mixin.border(0, --color-transparent, 4);
			color: var(--nuetral-color-700);
			padding: 0 0.8rem;
			white-space: nowrap;
		}
	}

	.contentpage__indexes--container {
		h4 {
			font-family: var(--font-secondary);
			font-weight: 700;
			font-size: 3.4rem;
			padding: 1.5rem 0;
			margin-bottom: 3rem;
			text-transform: uppercase;
			word-spacing: 2.5;
			background-color: var(--gray-color-400);
			color: var(--stone-color-700);
			@extend %center-me-full;

			@include bp.mq(md) {
				word-spacing: 1;
				font-size: 2rem;
			}

			@include bp.mq(sm) {
				font-size: 1.4rem;
			}
		}
	}
}
